<template>
  <div>
    <el-container>
      <el-aside width="auto">
        <div class="title">
          <div class="title-box">
            <img :src="imgLogo" />
            <p v-if="isCollapse == false">FF14</p>
          </div>

        </div>
        <el-menu :router="true" background-color="#545c64" text-color="#fff" active-text-color="#409eff"
          :collapse="isCollapse" :default-active="activeIndex" class="el-menu-vertical">
          <el-submenu index="1">
            <template #title>
              <i class="el-icon-setting"></i>
              <span>价格管理</span>
            </template>
            <el-menu-item index="/domain"><span>搜索</span></el-menu-item>
            
          </el-submenu>

        </el-menu>
      </el-aside>
      <el-container>
        <el-header>
          <div class="left link" @click="onClickFolding">
            <span>
              <i class="el-icon-more"></i>
            </span>
          </div>
        </el-header>
        <el-main>
          <router-view keep-alive transition="fade" transition-mode="out-in"></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
  <el-backtop :right="50" :bottom="50" />
</template>

<script>

import imgLogo from "./assets/images/logo1800.png";
import { useRouter } from 'vue-router';

export default {
  //mixins: [mixin],
  data() {
    return {
      route: '',
      isCollapse: false,
      activeIndex: "/domain",
      isShowAudit: false,
      isShowYun: false,
      imgLogo: imgLogo,
      
      selectData: [
        {
          value: "1",
          label: "中文",
        },
        {
          value: "2",
          label: "English",
        },
      ],
      selectValue: "中文",
    };
  },

  mounted() {
    var t = this;
    const { currentRoute } = useRouter();
    setTimeout(function () { 
      var url = currentRoute.value.path;
      var path = url.split("/")[1];
      var params = url.split("/")[3];
      t.activeMenu(path, params);
    }, 100)
  },

  watch: {
    $route(to, from) {
      var path = to.path.split("/")[1]; // web-rule-protection
      var params = to.path.split("/")[3]; // single_rule
      this.activeMenu(path, params);
    }
  },

  methods: {
    activeMenu(path, params) {
      var str = path;
      if (params) {
        str = path + "/" + params;
      }

      switch (str) {
        default:
          this.activeIndex = "/domain"
      }
    },
    onClickFolding() {
      this.isCollapse = !this.isCollapse;
    },
  
  },
};
</script>

